@font-face {
  font-family: "dia";
  src: url("ABCDiatypeMono-Bold.ttf");
}
@font-face {
  font-family: "m";
  src: url("Milton_Two.otf");
}
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* font-family: "dia"; */
  font-size: 26px;
  margin: 0;
}
.grid {
  display: grid;
  grid-template-columns: 240px auto;
  position: relative;
}
@media only screen and (max-width: 600px) {
  .grid {
    grid-template-columns: auto;
  }
}
.grid section {
  padding: 12px;
  border-bottom: 1px solid black;
}
#title {
  font-family: "m";
  position: absolute;
  width: 100%;
  font-size: 9vw;
  line-height: 9vw;
  text-align: center;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%) scale(2);
  animation: intro 3s linear;
  animation-fill-mode: forwards;
}
@keyframes intro {
  50% {
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%) scale(2);
  }
  100% {
    top: 1vw;
    left: 1vw;
    margin-right: 0%;
    transform: translate(0%, 0%) scale(1);
  }
}
input {
  position: absolute;
}
.menu {
  border: 1px solid black;
  padding: 4px;
  display: inline-block;
  margin: 12px 0 0px 0;
}
p:first-child {
  margin-top: 0;
}
.e {
  border: 1px solid black;
  display: inline-block;
  text-align: center;
  border-radius: 50%;
  line-height: 24px;
  margin: 0 8px 0 0;
  width: 24px;
  height: 24px;
}
hr {
  border: none;
  border-bottom: 1px solid black;
}
#mail-text span {
  position: absolute;
}
#tiles {
  top: 0;
  left: 0;
  height: 10vw;
}
#tiles div {
  border-right: 1px solid black;
  border-bottom: 1px solid black;
  width: 2.5vw;
  height: 2.5vw;
  box-sizing: border-box;
}
